<template>
    <div class="card-scene">
        <el-tabs v-model="activeName" @tab-click="resetPlay">
            <el-tab-pane label="Regel van vier" name="rules">
                <rules v-on:start="makeYourGroup"></rules>
            </el-tab-pane>
            <el-tab-pane label="Maak je groep" name="config">
                <el-row v-for="(family,index) in setupScene" :key="family.id" gutter="20" style="margin-bottom: 20px;">
                    <el-col :span="1">
                        <v-swatches v-model="family.color" popover-x="left"
                                    v-on:close="changeFamilyColor(family.id)"></v-swatches>
                    </el-col>
                    <el-col :span="6">
                        <el-input placeholder="family name" v-model="family.name" clearable
                                  v-on:change="saveGroup()"></el-input>
                    </el-col>
                    <el-col :span="1">
                        <el-button v-on:click="removeFamily(index)" type="text" class="remove-button">
                            <i class="fas fa-minus-circle"></i>
                        </el-button>
                    </el-col>
                    <el-col :span="12">
                        <el-row v-for="(men,indexMen) in family.people" :key="indexMen" gutter="20"
                                style="margin-bottom: 10px;">
                            <el-col :span="12">
                                <el-input placeholder="name" v-model="men.name" clearable
                                          v-on:change="saveGroup()"></el-input>
                            </el-col>
                            <el-col :span="6">
                                <el-select v-model="men.gender" placeholder="Select" v-on:change="saveGroup()">
                                    <el-option
                                            v-for="item in options"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-col>
                            <el-col :span="6" v-if="(indexMen != 0)">
                                <el-button v-on:click="removePerson(family.id,indexMen)" type="text"
                                           class="remove-button">
                                    <i class="fas fa-minus-circle"></i>
                                </el-button>
                            </el-col>
                        </el-row>
                        <el-row>
                            <el-col :span="12">
                                <el-button v-on:click="addPerson(family.id)" type="primary" plain>Add person</el-button>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
                <span v-if="(setupScene.length < 5)">
                    <el-divider></el-divider>
                    <el-row>
                        <el-col :span="24">
                            <el-button v-on:click="addFamily" type="primary" plain>Add household</el-button>
                        </el-col>
                    </el-row>
                </span>
                <div style="min-height:550px;">&nbsp;</div>
            </el-tab-pane>
            <el-tab-pane label="Simuleer" name="play" v-loading="isValidating">
                <Container
                        orientation="horizontal"
                        @drop="onColumnDrop($event)"
                        drag-handle-selector=".column-drag-handle"
                        @drag-start="dragStart"
                        :drop-placeholder="upperDropPlaceholderOptions">
                    <Draggable v-for="(family,index) in scene.families" :key="index">
                        <div class="card-container" :class="family.status">
                            <div class="card-column-header">
                                <span class="column-drag-handle">&#x2630;</span>
                                {{ family.name }}
                            </div>
                            <br clear="all">
                            <Container
                                    group-name="col"
                                    @drop="(e) => onCardDrop(family.id, e)"
                                    :get-child-payload="getCardPayload(family.id)"
                                    drag-class="card-ghost"
                                    drop-class="card-ghost-drop"
                                    :drop-placeholder="dropPlaceholderOptions">
                                <Draggable v-for="(men,indexMen) in family.people" :key="indexMen">
                                    <div class="people-wrapper">
                                        <div :id="men.gender">
                                            <!-- distance -->
                                            <div class="arrow-left" :style="'color:'+men.color">
                                                <i class="fas fa-arrow-left" v-show="(men.isDistance)"></i>
                                            </div>
                                            <!-- status -->
                                            <div class="icon" :style="'color:'+men.color" v-if="(men.mood == 'shitty')">
                                                <i class="fas fa-poo"></i>
                                            </div>
                                            <div class="icon" :style="'color:'+men.color" style="font-size: 5em;"
                                                 v-else-if="(men.mood == 'angry')">
                                                <i class="far fa-angry"></i>
                                            </div>
                                            <div class="icon" :style="'color:'+men.color" style="font-size: 4em;"
                                                 v-else-if="(men.mood == 'party')">
                                                <i class="far fa-grin-tongue-squint"></i>
                                            </div>
                                            <div class="icon" :style="'color:'+men.color" v-else>
                                                <i class="fas" :class="men.gender"></i>
                                            </div>
                                            <!-- distance -->
                                            <div class="arrow-right" :style="'color:'+men.color">
                                                <i class="fas fa-arrow-right" v-show="(men.isDistance)"></i>
                                            </div>
                                        </div>
                                        <br clear="all">
                                        <small :style="'color:'+men.color" style="margin-left:10px;">
                                            <strong>{{men.name}}</strong>
                                        </small>
                                    </div>
                                </Draggable>
                            </Container>
                        </div>
                    </Draggable>
                </Container>
                <div>
                    <br><el-alert effect="dark" type="info"
                            title="Sleep de personen en dieren naar een ander huishouden.">
                    </el-alert>
                </div>
                <div style="padding-top: 50px;">&nbsp;</div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>

    import {Container, Draggable} from 'vue-smooth-dnd'
    import {applyDrag} from '../utils/helpers'
    import VSwatches from 'vue-swatches'
    import Rules from "./rules";

    export default {
        name: 'ruleOfFour',
        components: {Rules, Container, Draggable, VSwatches},
        mounted() {
            const setupScene = JSON.parse(this.$localStorage.get('yoursocialgroup'));
            if (setupScene) {
                this.setupScene = setupScene;
            }
            this.scene.families = JSON.parse(JSON.stringify(this.setupScene));
        },
        data() {
            return {
                options: [{
                    value: 'fa-male',
                    label: 'Male'
                }, {
                    value: 'fa-female',
                    label: 'Female'
                }, {
                    value: 'fa-child',
                    label: 'Child'
                }, {
                    value: 'fa-baby',
                    label: 'Baby'
                }, {
                    value: 'fa-dog',
                    label: 'Dog'
                }, {
                    value: 'fa-cat',
                    label: 'Cat'
                }],
                activeName: 'rules',
                setupScene: [],
                scene,
                upperDropPlaceholderOptions: {
                    className: 'cards-drop-preview',
                    animationDuration: '150',
                    showOnTop: true
                },
                dropPlaceholderOptions: {
                    className: 'drop-preview',
                    animationDuration: '150',
                    showOnTop: true
                }
                , isValidating: false
            }
        },
        methods: {
            makeYourGroup() {
                this.activeName = 'config';
            }
            , addFamily() {
                let id = this.setupScene.length + 1;
                let family = {
                    id: id
                    , name: ''
                    , color: '#000000'
                    , isHome: false
                    , status: 'normal'
                    , type: 'container'
                    , props: {
                        orientation: 'vertical'
                        , className: 'card-container'
                    }
                    , people: [{
                        id: id + '-1'
                        , familyId: id
                        , name: ''
                        , mood: 'normal'                // angry // shitty // party
                        , isDistance: false
                        , isSingle: false
                        , gender: 'fa-male'
                        , color: '#000000'
                        , type: 'draggable'
                        , props: {
                            className: 'card',
                        }
                    }]
                };
                this.setupScene.push(family);
                this.saveGroup();
            }
            , removeFamily(index) {
                this.setupScene.splice(index, 1);
                this.saveGroup();
            }
            , addPerson(familyId) {
                let family = this.findFamily(familyId);
                let men = {
                    id: familyId + '-1'
                    , familyId: familyId
                    , name: ''
                    , gender: 'fa-female'
                    , color: family.color
                    , type: 'draggable'
                    , mood: 'normal'
                    , isDistance: false
                    , isSingle: false
                    , props: {
                        className: 'card',
                    }
                }
                family.people.push(men);
                this.saveGroup();
            }
            , removePerson(familyId, index) {
                let family = this.findFamily(familyId);
                family.people.splice(index, 1);
                this.saveGroup();
            }
            , changeFamilyColor(familyId) {
                let family = this.findFamily(familyId);
                for (let i = 0; i < family.people.length; i++) {
                    family.people[i].color = family.color;
                }
                this.saveGroup();
            }
            , findFamily(familyId) {
                for (let i = 0; i < this.setupScene.length; i++) {
                    if (this.setupScene[i].id == familyId) return this.setupScene[i];
                }
            }
            , saveGroup() {
                for (let i = 0; i < this.setupScene.length; i++) {
                    if (this.setupScene[i].people.length == 1) {
                        this.setupScene[i].people[0].isSingle = true;
                    } else {
                        for (let j = 0; j < this.setupScene[i].people.length; j++) {
                            this.setupScene[i].people[j].isSingle = false;
                        }
                    }
                }
                this.$localStorage.set('yoursocialgroup', JSON.stringify(this.setupScene))
            }
            , resetPlay(tab) {
                if (tab.name == 'play') {
                    this.scene.families = JSON.parse(JSON.stringify(this.setupScene));
                }
            }
            // ------------------------------------------------------------------------------------- drag and drop stuff
            , onColumnDrop(dropResult) {
                const scene = Object.assign({}, this.scene)
                scene.families = applyDrag(scene.families, dropResult)
                this.scene = scene
            }
            , onCardDrop(columnId, dropResult) {
                if (dropResult.removedIndex !== null || dropResult.addedIndex !== null) {
                    const scene = Object.assign({}, this.scene)
                    const column = scene.families.filter(p => p.id === columnId)[0]
                    const columnIndex = scene.families.indexOf(column)
                    const newColumn = Object.assign({}, column)
                    newColumn.people = applyDrag(newColumn.people, dropResult)
                    scene.families.splice(columnIndex, 1, newColumn)
                    this.scene = scene
                    this.validateScene()
                }
            }
            , getCardPayload(columnId) {
                return index => {
                    return this.scene.families.filter(p => p.id === columnId)[0].people[index]
                }
            }
            // , startDragMen() {}
            // , stopDragMen() {}
            // , dragStart() {
            // console.log('drag started')
            // }
            // , log() { // ...params
            // console.log(...params)
            // }
            // ------------------------------------------------------------------------------------------ validate scene
            , validateScene() {
                // if (!this.isValidating) {
                this.isValidating = true;
                for (let i = 0; i < this.scene.families.length; i++) {
                    this.validateFamily(this.scene.families[i]);
                }
                // setTimeout(function () {
                this.isValidating = false;
                // }.bind(this), 500);
                // }
            }
            , validateFamily(family) {
                this.setDistanceInFamily(family, !this.isFamilyAloneAtHome(family));
                this.setParty(family, this.areKidsAlone(family));
                this.setMoodShitty(family, this.isDogOrBabyAlone(family));
                this.setPersonAlone(family, this.isPersonAlone(family));
                this.validateFamilySituation(family);
            }
            // --------------------------------------------------------------------------------- validate scene checkers
            , isFamilyAloneAtHome(family) {
                for (let i = 0; i < family.people.length; i++) {
                    if (family.people[i].familyId != family.id) return false;
                }
                return true;
            }
            , setDistanceInFamily(family, isDistance) {
                for (let i = 0; i < family.people.length; i++) {
                    if (family.people[i].gender == 'fa-male'
                        || family.people[i].gender == 'fa-female'
                        || family.people[i].gender == 'fa-child'
                        || family.people[i].gender == 'fa-baby') {
                        family.people[i].isDistance = isDistance;
                    }
                }
            }
            , isDogOrBabyAlone(family) {
                for (let i = 0; i < family.people.length; i++) {
                    if (family.people[i].gender == 'fa-male'
                        || family.people[i].gender == 'fa-female'
                        || family.people[i].gender == 'fa-child'
                    ) return false;
                }
                return true;
            }
            , setMoodShitty(family, isShitty) {
                for (let i = 0; i < family.people.length; i++) {
                    if (isShitty && (family.people[i].gender == 'fa-dog' || family.people[i].gender == 'fa-baby')) {
                        family.people[i].mood = 'shitty';
                    } else if (family.people[i].gender == 'fa-dog' || family.people[i].gender == 'fa-baby') {
                        family.people[i].mood = 'normal';
                    }
                }
            }
            , areKidsAlone(family) {
                for (let i = 0; i < family.people.length; i++) {
                    if (family.people[i].gender == 'fa-male'
                        || family.people[i].gender == 'fa-female') return false;
                }
                return true;
            }
            , setParty(family, isParty) {
                for (let i = 0; i < family.people.length; i++) {
                    if (isParty && family.people[i].gender == 'fa-child') {
                        family.people[i].mood = 'party';
                    } else if (family.people[i].gender == 'fa-child') {
                        family.people[i].mood = 'normal';
                    }
                }
            }
            , isPersonAlone(family) {
                if (family.people.length == 1) {
                    if (family.people[0].gender == 'fa-male' || family.people[0].gender == 'fa-female') return true
                }
                return false
            }
            , setPersonAlone(family, isAlone) {
                for (let i = 0; i < family.people.length; i++) {
                    if (isAlone && !family.people[i].isSingle && (family.people[i].gender == 'fa-male' || family.people[i].gender == 'fa-female')) {
                        family.people[i].mood = 'angry';
                    } else if (family.people[i].gender == 'fa-male' || family.people[i].gender == 'fa-female') {
                        family.people[i].mood = 'normal';
                    }
                }
            }
            , validateFamilySituation(family) {
                let homeCounter = 0;
                let guestCounter = 0;
                for (let i = 0; i < family.people.length; i++) {
                    let man = family.people[i]
                    if (!(man.gender == 'fa-dog' || man.gender == 'fa-cat')) {
                        if (man.familyId == family.id) {
                            homeCounter = 1;
                        } else {
                            guestCounter++;
                        }
                    }
                }
                let total = homeCounter + guestCounter;
                if (total > 5) {
                    family.status = 'invalid'
                } else {
                    family.status = 'normal'
                }
            }
        }
    }

    const scene = {
        type: 'container',
        props: {orientation: 'horizontal'},
        families: []
    }

</script>

<style scoped>

    .arrow-left {
        border-top: 1px solid rgba(0, 0, 0, 0);
        display: block;
        float: left;
        width: 33% !important;
        font-size: 3em;
    }

    .arrow-right {
        /*border:1px solid black;*/
        display: block;
        float: left;
        width: 25%;
        text-align: right;
        font-size: 3em;
    }

    .icon {
        /*border:1px solid black;*/
        display: block;
        text-align: center;
        float: left;
        width: 40%;
    }

    #fa-male .arrow-left, #fa-female .arrow-left, #fa-male .arrow-right, #fa-female .arrow-right {
        margin-top: 40px;
    }

    #fa-child .arrow-left, #fa-child .arrow-right {
        margin-top: 25px;
    }

    #fa-male .icon, #fa-female .icon {
        font-size: 7em;
    }

    #fa-child .icon {
        font-size: 5.5em;
    }

    #fa-baby .icon {
        font-size: 4em;
    }

    #fa-cat .icon, #fa-dog .icon {
        font-size: 3em;
    }

    .people-wrapper {
        clear: both;
        display: block;
        width: 100%;
        text-align: center;
    }

    .remove-button {
        color: silver;
    }

    .remove-button:hover {
        color: orangered;
    }

    .invalid {
        background-image: linear-gradient(45deg, #ff0303 25%, #c20000 25%, #c20000 50%, #ff0303 50%, #ff0303 75%, #c20000 75%, #c20000 100%);
        background-size: 28.28px 28.28px;
    }

</style>
